.App {
  text-align: left;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.drawer-button {
  display: block;
  color: unset;
  text-decoration: none;
  padding: 1em;
  /* margin: 1em 0 1em 0; */
  margin: 0;
}

.drawer-button:hover {
  background: gray;
}

.apart {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
